import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { fail } from 'assert';
import {MatButtonModule} from '@angular/material/button';


@Component({
  selector: 'story',
  templateUrl: './story.component.html',
  styleUrls: ['./story.component.css'],
  
})
export class StoryComponent implements OnInit {
 private numberofdone:number=0;
 private numberofanalysis:number=0;
 private numberoftesting:number=0;
 private numberofcoding:number=0;
 private percentageofdone:number=0;

  constructor() { }

  ngOnInit() {

  }
  @Input()
  public defects : any ;
  @Output()
  public delete = new EventEmitter<any>();
  @Output()
  public update = new EventEmitter<any>();
  @Output()
  public block = new EventEmitter<any>();


   selectAnalysis(defect){
   defect.block=false;
    defect.analysis=true;
    defect.coding=false;
    defect.done = false;
    defect.testing=false;
    this.updateMe(defect);
  };
  selectCoding(defect){
    defect.block=false;
    defect.analysis=true;
    defect.coding=true;
    defect.done = false;
    defect.testing=false;
    this.updateMe(defect);
  };  selectTesting(defect){
    defect.block=false;
    defect.analysis=true;
    defect.coding=true;
    defect.done = false;
    defect.testing=true;
    this.updateMe(defect);
  };  selectDone(defect){
    defect.block=false;
    defect.analysis=true;
    defect.coding=true;
    defect.done = true;
    defect.testing=true;
    this.updateMe(defect);
  }; 

  deleteMe(defect){
  this.delete.emit({defect:defect});
  };
  updateMe(defect){
    this.summary();
    this.update.emit({defect:defect});
    };
  blockMe(defect){
    if(defect.block){
      defect.block=false;
      defect.analysis=true;
    }
    else{
      defect.block=true;
      defect.analysis=false;
      defect.coding=false;
      defect.done = false;
      defect.testing=false;
    }

      this.updateMe(defect);
      };

summary(){
    var done =0;
    var testing = 0;
    var analysis = 0;
    var coding =0;
    for (var i = 0, len = this.defects.length; i < len; i++) {
            if(this.defects[i].done){
              done=done +1;
            }
            if(this.defects[i].analysis){
              analysis=analysis +1;
            }
            if(this.defects[i].coding){
              coding=coding +1;
            }
            if(this.defects[i].testing){
              testing=testing +1;
            }
    }
    this.numberofdone=done;
    this.numberoftesting=testing-done;
    this.numberofcoding=coding-testing;
    this.numberofanalysis=analysis-coding;
    this.percentageofdone=(this.numberofdone/this.defects.length)*100;
}

}




